import React from 'react';
import { Redirect } from 'react-router-dom'
import StyledFirebaseAuth from 'react-firebaseui/StyledFirebaseAuth';
import firebase from 'firebase/app';
import useFirebaseUser from '../firebase/FirebaseUser';

const uiConfig = {
  signInFlow: 'popup',
  signInOptions: [
    firebase.auth.GoogleAuthProvider.PROVIDER_ID
  ],
  callbacks: {
    signInSuccessWithAuthResult: () => false
  }
}

function LoginScreen() {
  const userState = useFirebaseUser()

  React.useEffect(() => {
    if (userState.status === 'UNAUTHORIZED') {
      userState.user.delete().catch((error) => {
        console.log("An error occurred while deleting an unauthorized user. ", error);
      })
    }
  }, [userState])

  if (userState.status === 'UNAUTHORIZED') {
    return <Redirect to="/unauthorized" />
  }
  else if (userState.status === 'LOGGED_IN') {
    return <Redirect to="/" />
  }
  else {
    return (
      <div>
        <StyledFirebaseAuth uiConfig={uiConfig} firebaseAuth={firebase.auth()} />
      </div>
    )
  }
}

export default LoginScreen
