import React from 'react'
import firebase from 'firebase/app'
import firebaseApp from '../firebase/FirebaseConfig'

import 'firebase/auth'
import 'firebase/firestore'

const db = firebase.firestore(firebaseApp)

function userIsAuthorized(email) {
  const docRef = db.collection('users').doc(email)

  return new Promise((resolve, reject) => {
    docRef.get().then((doc) => {
      if (doc.exists && doc.data()['admin'] === true) {
        resolve(true)
      }
      else {
        resolve(false)
      }
    }).catch((error) => {
      console.error("Error getting document: ", error)
      reject(error)
    })
  })
}

function firebaseUserReducer(state, action) {
  switch (action.type) {
    case 'LOGGED_OUT':
      return {
        user: null,
        status: 'LOGGED_OUT'
      }
    case 'PENDING':
      return {
        user: action.user,
        status: 'PENDING'
      }
    case 'LOGGED_IN':
      return {
        user: action.user,
        status: 'LOGGED_IN'
      }
    case 'UNAUTHORIZED':
      return {
        user: action.user,
        status: 'UNAUTHORIZED'
      }
    default:
      throw new Error()
  }
}

/**
 * Custom hook for accessing the current firebase user, if one is logged in.
 */
function useFirebaseUser() {
  const [state, dispatch] = React.useReducer(firebaseUserReducer, {
    user: null,
    status: 'LOGGED_OUT'
  })

  React.useEffect(() => {
    return firebase.auth().onAuthStateChanged((user) => {
      if (user === null) {
        dispatch({ type: 'LOGGED_OUT' })
      }
      else {
        dispatch({ type: 'PENDING', user })
        userIsAuthorized(user.email).then((isAuthorized) => {
          if (isAuthorized) {
            dispatch({ type: 'LOGGED_IN', user })
          }
          else {
            dispatch({ type: 'UNAUTHORIZED', user })
          }
        })
      }
    });
  }, [])

  return state
}

export default useFirebaseUser
