import React from 'react';
import firebase from 'firebase/app';
import useFirebaseUser from '../firebase/FirebaseUser';

function UserStatus() {

  const userState = useFirebaseUser()

  if (userState.status === 'LOGGED_IN') {
    return (
      <form className="form-inline my-2 my-lg-0">
        <span className="text-light my-2 mr-sm-2">{userState.user.email}</span>
        <button className="btn btn-success my-2 my-sm-0" onClick={() => firebase.auth().signOut()} type="submit">Logout</button>
      </form>
    )
  }

  return (
    <></>
  )
}

export default UserStatus
