import React from 'react'
import useFirebaseUser from '../firebase/FirebaseUser'

function Footer() {

  const userState = useFirebaseUser()

  let loginButton
  if (userState.status === 'LOGGED_OUT') {
    loginButton = <a className="text-muted" href="#/login">Login</a>
  }

  return (
    <footer className="page-footer font-small bg-light fixed-bottom">
      <div className="container">
        <div className="footer-copyright text-center py-2">Â© 2019 Copyright:&nbsp;
          <a href={process.env.PUBLIC_URL}>{process.env.PUBLIC_URL.replace('https://', '')}</a>
        </div>
        <div className="text-center py-2">
          {loginButton}
        </div>
      </div>
    </footer>
  )
}

export default Footer
